<script>
import { Pie, Bar } from "vue-chartjs";


export default {
  props: ["labels", "label", "data"],
  extends: Pie,
  mounted() {
    // Overwriting base render method with actual data.
    this.renderMyChart();
  },
  methods: {
    renderMyChart() {
      this.renderChart({
        labels: this.labels,
        datasets: [
          {
            label: "people answered",
            backgroundColor: this.labels.map(() => getRandomColor()),
            data: this.data
          }
        ]
      });
    }
  },
  watch:{
    data(){
      this.renderMyChart();
    }
  }
};

function getRandomColor() {
  var letters = '0123456789ABCDEF';
  var color = '#';
  for (var i = 0; i < 6; i++) {
    color += letters[Math.floor(Math.random() * 16)];
  }
  return color;
}
</script>
